.businessBlock {
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.196643);
    margin: 10px 0;
    &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 17px;

        &-title {
            display: flex;
            align-items: center;

            h3 {
                font-style: normal;
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                color: #414D6A;
                margin-left: 16px;
            }
        }

        &-price {
            span {
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #414D6A;
            }
        }

        &-date {
            span {
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #414D6A;
            }
        }

        &-btnWrapper {
            display: flex;
            align-items: center;

            button {
                margin-left: 22px;
            }


            &-up {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                color: #000000;
                padding: 8px 20px 8px 47px;
                background: #FFFFFF;
                border: 1px solid #41D481;
                box-sizing: border-box;
                border-radius: 100px 5px 5px 100px;
                position: relative;
                display: flex;
                align-items: center;
                span{
                    width: 36px;
                    height: 36px;
                    position: absolute;
                    left: -5px;
                    border-radius: 50%;
                    background: #41D481;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    svg {
                        
                        position: absolute;
                        bottom: 0px;
                        fill: white;
                    }
                }
            }

            &-rek {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                color: #000000;

                padding: 8px 20px 8px 47px;
                background: #FFFFFF;
                border: 1px solid #304FFF;
                box-sizing: border-box;
                border-radius: 100px 5px 5px 100px;
                position: relative;
                display: flex;
                align-items: center;

                span {
                    width: 36px;
                    height: 36px;
                    position: absolute;
                    left: -5px;
                    border-radius: 50%;
                    background: #304FFF;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    svg {

                        position: absolute;
                        bottom: 0px;
                        fill: white;
                    }
                }

            }

            &-menu {
                width: 36px;
                height: 36px;
                background: #FFFFFF;
                box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.196643);
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                border: none;

                span {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                svg {
                    height: 20px;
                    width: 20px;
                    transform: rotate(90deg);
                }
            }
        }
    }

    &-bottom {
        border-top: 1px solid rgba(151, 151, 151, 0.246886);
        padding: 10px 17px;
            span {
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #414D6A;
                margin-right: 20px;

                
            }
        
    }
}