.tokenHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 30px;

    h3 {
        font-style: normal;
        font-weight: bold;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #414D6A;
    }

    h4 {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #304FFF;
        margin-right: 36px;
    }

    button {
        cursor: pointer;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        background: #FFFFFF;
        border: 1px solid #41D481;
        box-sizing: border-box;
        border-radius: 100px 15px 15px 100px;
        padding: 8px 12px 8px 47px;
        display: flex;
        align-items: center;
        position: relative;



        span {
            display: flex;
            align-items: center;
            justify-content: center;
            background: #41D481;
            position: absolute;
            color: black;
            border-radius: 50%;
            width: 35px;
            height: 35px;
            left: 0;
        }
    }

    

    &-right {
        display: flex;
        align-items: center;
        
    }
}