.footerSlider {


    &-header {
        border-bottom: 1px solid #D5D5D5;
        margin-bottom: 10px;
        padding: 10px 0;

        .one, .two, .three {
            font-style: normal;
            font-weight: 900;
            font-size: 24px;
            line-height: 30px;
            color: #9B9B9B;
            text-decoration: none;
            margin-right: 77px;
            position: relative;
            padding: 10px 0;
            transition: all ease 0.5s;

            &::before {
                content: "";
                position: absolute;
                width: 0;
                height: 2px;
                margin: 0 auto;
                background: #4E72CA;
                bottom: 0;
                left: 0;
                right: 0;
                transition: all ease 0.5s;
            }

            &:hover {
                color: #4E72CA;
                transition: all ease 0.5s;


                &::before {
                    width: 50%;
                    transition: all ease 0.5s;
                }
            }

            &.active {
                color: #4E72CA;
                transition: all ease 0.5s;


                &::before {
                    width: 50%;
                    transition: all ease 0.5s;
                }
            }
        }
    }

    &-footer {
        margin-top: 30px;
        border-top: 1px solid rgba(182, 202, 251, 0.606827);
        padding: 18px 0;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;


        h4 {
            font-size: 15px;
            line-height: 18px;
            color: #4E72CA;
            margin-right: 10px;
        }
        
        svg {
            transform: rotate(90deg);
            width: 10px;
            height: 10px;
            fill: #4E72CA;

        }
    }
}