.productInfoBlock {
    display: flex;
    flex-direction: column;
    width: 190px;

    padding: 14px;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(151, 151, 151, 0.250226);
    border-left: 1px solid rgba(151, 151, 151, 0.250226);

    h4 {
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -0.00016833px;
        color: #444444;
    }

    span {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: -0.00016833px;
        color: #444444;
        margin-top: 5px;
    }
}