.btnBlock {

    margin-top: 22px;
    overflow: hidden;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 22;
    box-shadow: 0 0 10px black;
    background: white;
    display: none;
    align-items: center;
    justify-content: space-between;
    padding: 14px 44px;

    &-column {
        display: block;
    }

    a {
        text-decoration: none;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 39px;
        letter-spacing: -6.7332e-05px;
        color: #000000;


        &::before {
            content: "";
            position: absolute;
            width: 10px;
            height: 10px;
            background: #B4E2E1;
            border-radius: 50%;
            border: 1px solid #4A90E2;
            left: -24px;
        }
    }
}



