

.footerUnder {
    display: flex;
    justify-content: space-between;

    &-column {
        display: flex;
        flex-direction: column;

        &-items {
            display: flex;
            flex-direction: column;
            margin-bottom: 30px;
        }
    }

    h4 {
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        margin-bottom: 24px;
        display: flex;
        align-items: center;
        position: relative;


        svg {
            position: absolute;
            left: -20px;
            width: 15px;
            height: 15px;
            fill: green;
        }

    
    }

    a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 29px;
        color: #878F99;
        text-decoration: none;

        &:hover {
            text-decoration: solid;
        }
    }
}