.header-inner {
    display: flex;
    flex-direction: column;



    &-row1 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 16px;

        &__logo {
            display: flex;
            align-items: center;
            font-style: normal;
            font-weight: bold;
            font-size: 15px;
            line-height: 17px;
            color: #000000;
            text-decoration: none;

            svg {
                margin-right: 14px;
            }
        }

        &-links {

            a{
                text-decoration: none;
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #000000;
                margin: 0 30px;
            }
        }

        &-right {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 350px;

            &-items {
                a {
                    margin:  0 15px;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 17px;
                    /* identical to box height */


                    color: #4A4A4A;
                }

                &__rus {
                    position: relative;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    &::before {
                        content: "";
                        position: absolute;
                        width: 150%;
                        height: 5px;
                        background: #f8e0cc97;
                        transform: rotate(-45deg);
                        
                        
                    }
                }

                .user {
                    position: relative;
                    display: flex;
                    align-items: center;
                    
                    .user1 {
                        cursor: pointer;
                        background: white;
                        display: flex;
                        align-items: center;
                        text-decoration: none;
                        position: relative;
                        z-index: 2;
                        border-radius: 22px;

                        

                        h3 {
                            margin-right: 10px;
                            font-style: normal;
                            font-weight: 500;
                            font-size: 17px;
                            line-height: 21px;
                            text-align: center;
                            color: #787993;
                        }

                        img {
                            width: 41px;
                            height: 41px;
                        }
                    }

                    .exit {
                        position: absolute;
                        background: #19D476;
                        border-radius: 22px;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 14px;
                        line-height: 17px;
                        width: 50%;
                        
                        color: #FFFFFF;
                        top: 2px;
                        display: flex;
                        align-items: center;
                        justify-content:flex-end;
                        padding: 10px 20px;
                        text-decoration: none;
                        z-index: 1;
                        transition: all ease 0.5s;

                        &.active {
                            transform: translateX(80px);
                            transition: all ease 0.5s;
                        }

                        
                        
                    }
                }

                &-yes {
                    a {
                        font-style: normal;
                        font-weight: bold;
                        font-size: 17px;
                        line-height: 21px;
                        /* identical to box height */


                        color: #4E72CA;
                        &:first-child {
                            text-decoration: none;
                        }
                    }
                }
            }
        }
    }

    &-row2{

    }
}