.chatUser {
    margin-top: 10px;
    display: flex;
    align-items: center;

    &-title {
        margin-right: 17px;

        h4 {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 22px;
            color: #25252B;
        }
    }
}

.businessSms {
    display: flex;
    width: 1200px;

    &-right {
        padding: 32px 72px;
        box-sizing: border-box;
        display: flex;
        width: 765px;

        &-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 690px;

            &-left {
                display: flex;
                align-items: center;

                &-img {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: 16px;
                }

                h3 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 17px;
                    letter-spacing: 0.233333px;
                    color: #25252B;
                    padding: 2px 0;
                    border-bottom: 1px solid #979797a0;

                }
            }

            &-right {
                display: flex;
                align-items: center;
                justify-content: space-between;
                


                svg {
                    width: 20px;
                    height: 20px;
                    margin-left: 16px;
                    cursor: pointer;

                    &:last-child{
                        opacity: 0.5;
                    }
                }
            }
        }

        &-body {
            margin-top: 24px;

            &-top {
                span {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 16px;
                    color: #686871;

                }
            }

            &-row1 {
                margin-top: 150px;
                padding-bottom: 40px;
                border-bottom: 1px solid #979797;
            }

            &-chat {
                width: 690px;
                height: 290px;
                overflow-y: scroll;
                display: flex;
                align-items: flex-end;
                justify-content: flex-start;
                flex-direction: column;


                &::-webkit-scrollbar {
                    width: 0 !important;
                    height: 0;
                    display: none !important;
                    background: transparent;
                }
            }

            &-footer {
                textarea {
                    background: #FFFFFF;
                    border: 1px solid #EBECF0;
                    border-radius: 5px;
                    width: 100%;
                    height: 109px;
                    resize: none;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 22px;
                    color: #25252B;
                }

                &-row1 {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-top: 16px;

                    svg {
                        width: 20px;
                        height: 20px;
                    }
                    

                    button {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 17px;
                        color: #FFFFFF;
                        background: #41D481;
                        box-shadow: 0px 2px 24px rgba(72, 128, 255, 0.2);
                        border-radius: 20px;
                        border: none;
                        padding: 10px 20px;
                        box-sizing: border-box;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        span {

                            display: flex;
                            align-items: center;
                            justify-content: center;
                            margin-left: 8px;

                            svg {
                                transform: rotate(90deg);
                                
                                width: 16px;
                                height: 16px;
                            }
                        }

                    }
                }
            }
        }
    }
}