.header-filter {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-left {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        box-sizing: border-box;
        border-radius: 25px;


        select {
            border: none;
            padding: 15px 30px;
            box-sizing: border-box;
            border-radius: 25px;
            outline: none;
            font-style: normal;
            font-weight: 500;
            font-size: 15px;
            line-height: 18px;
            color: #000000;
            margin-right: 10px;

            
        }
        
        input {
            border: none;
            padding: 15px 30px;
            box-sizing: border-box;
            border-left: 1px solid rgba(151, 151, 151, 0.496575);
            border-right: 1px solid rgba(151, 151, 151, 0.496575);
            outline: none;
            font-style: normal;
            font-weight: 500;
            font-size: 15px;
            line-height: 18px;

            color: #000000;
        }

        button {
            background: #19D476;
            border-radius: 25px;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            padding: 16px 20px;
            color: #FFFFFF;
            border: 1px solid #19D476;
            outline: none;
            margin-left: 10px;
        }
    }

    &-right {
        button {
            font-style: normal;
            font-weight: bold;
            font-size: 15px;
            line-height: 19px;
            color: #FFFFFF;
            background: #19D476;
            border-radius: 25px;
            padding: 16px 30px 16px 56px;
            outline: none;
            border: none;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;

            &::before{
                content: "+";
                display: flex;
                align-items: center;
                justify-content: center;
                width: 40px;
                height: 40px;
                position: absolute;
                font-size: 28px;
                line-height: 33px;
                color: #FFFFFF;
                left: 0;
                background: #11B463;
                border-radius: 50%;
                padding: 5px;
            }


        }
    }
}