.footer {

    &-top {
        background: #282B30;
        padding: 22px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-items {
            display: flex;
            align-items: center;
            justify-content: space-between;

            &__link {
                margin-right: 14px;
            }

            p {
                font-style: normal;
                font-weight: bold;
                font-size: 14px;
                line-height: 17px;
                color: #FFFFFF;
                mix-blend-mode: normal;
                opacity: 0.1;
                &:first-child {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #98A4B7;
                    opacity: 1;

                }
            }

            &__btns {
                margin-right: 4px;
            }
        }
    }

    &-bottom {
        box-sizing: border-box;
        padding-top: 50px;
        background: #33383C;
    }
}