

body {
    margin: 0;
    padding: 0;
    font-family: 'Gilroy';
    
}

h1, h2, h3, h4, h5, h6, p, a {
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1200px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}

.header {
    background: #FFFFFF;
    box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
    padding: 17px 0;
}

.footer {
    background: #282B30;
}



