.signup1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


    &-top {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        h3 {
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 30px;
            text-align: center;
            text-transform: uppercase;
            color: #000000;
        }
    }

    &-center {
        margin-top: 75px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        input {
            font-style: normal;
            font-weight: 500;
            font-size: 19px;
            line-height: 23px;
            color: black;
            background: #FFFFFF;
            border: 1px solid rgba(151, 151, 151, 0.482592);
            box-sizing: border-box;
            border-radius: 5px;
            padding: 20px 25px;
            width: 100%;
            outline: none;
            
            &::placeholder {
                color: #C7C7C7;

            }
        }

        a {
            outline: none;
            margin-top: 26px;
            background: #41D481;
            border-radius: 5px;
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            text-align: center;
            color: #FFFFFF;
            padding: 21px 130px;
            text-decoration: none;
        }
    }

    &-bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        margin-top: 50px;

        span {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            text-align: center;
            color: #787993;
            width: 100%;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;

            &::before {
                content: "";
                position: absolute;
                width: 40%;
                left: 0;
                height: 1px;
                background: #979797;
            }

            &:after {
                content: "";
                position: absolute;
                width: 40%;
                right: 0;
                height: 1px;
                background: #979797;
            }
        }

        a {
            margin-top: 33px;
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            text-decoration: none;
            color: #4E72CA;
        }

    }
}

