.productBlock {
    max-width: 250px;
    display: block;
    text-decoration: none;
    

    &-image {
        img {
            border-radius: 8px;
            max-width: 250px;
        }
    }

    &-info {
        padding: 2px;
        box-sizing: border-box;
        h4 {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
        }

        span {
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 14px;
            color: #4A4A4A;
        }

        h3 {
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            color: #000000;
        }
    }

    &:hover{
        opacity: 0.3;
    }
}