.productInfo {
    &-top {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 30px;
    }

    &-bottom {
        border-left: 1px solid rgba(151, 151, 151, 0.191604);
        padding: 14px;
        


        h4 {
            font-size: 14px;
            line-height: 16px;
            letter-spacing: -0.00016833px;
            color: #444444;
            margin-bottom: 5px;
        }   

        a {
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: -0.00016833px;
            text-decoration-line: underline;
            color: #4E72CA;
            margin-bottom: 100px;

        }
    }
}