.footerContainer {
    &-top {
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 34px;
        h4 {
            font-size: 15px;
            line-height: 18px;
            color: #66717A;
            margin-right: 10px;
            cursor: pointer;
        }

        svg {
            width: 10px;
            height: 10px;
            fill: #66717A;
            transform: rotate(90deg);

        }
    }

    &-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top: 1px solid #4C565E;
        padding: 27px 0;
        box-sizing: border-box;

        a {
            font-style: normal;
            text-decoration: none;
            font-size: 15px;
            line-height: 29px;
            color: #878F99;

            &:hover {
                color: #05F3B3;
            }
        }
    }
}