.btnSlider {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    margin-top: 37px;

    button {
        cursor: pointer;
        &.next {
            position: absolute;
            left: -35px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 34px;
            height: 34px;
            background: #FFFFFF;
            border-radius: 50%;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
            border: none;


            svg {
                fill: #4E72CA;
                transform: rotate(0);
                position: absolute;
                right: 0;

            }
        }

        &.prev {
            position: absolute;
            right: -35px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 34px;
            height: 34px;
            background: #FFFFFF;
            border-radius: 50%;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
            border: none;
            

            svg {
                fill: #4E72CA;
                transform: rotate(180deg);
                position: absolute;
                left: 0;

            }
        }
    }

    &-content {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

       
    }
}