.wrper {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgba(255, 255, 255, 0.721);
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btnBlockAdd {
    display: flex;
    width: 941px;
    box-shadow: 0 0 10px black;
    background: white;

    padding: 34px;
    box-sizing: border-box;
    &-column {
        display: flex;
        flex-direction: column;
        
        width: 290px;

        
        &:nth-child(2){display: none;}
        &:nth-child(3){display: none;}

        &.active1 {
            display: flex;

        }

        &.active2 {
            display: flex;
        }

        h3 {
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #000000;
            margin-bottom: 20px;

        }

        
        &-links {

            a {
                display: flex;
                flex-direction: column;
                text-decoration: none;
            }
            padding-left: 20px;
            display: flex;
            flex-direction: column;
            height: 100%;
            input {
                display: none;

                
            }
            
            &.me{
                border-left: 2px solid #979797;
            }

            input:checked + .label {
                color: #11B463;
            }

            label{
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 36px;
                color: #000000;
                cursor: pointer;

                &.active {
                    color: #11B463;
                }


            }

            
        }
    }
}