.home {

    &-slider {
        margin-bottom: 30px;
        .sliderBtns{
            position: relative;
            z-index: 21;
        }

        .btnBlock {
            
            margin-top: 22px;
            overflow: hidden;
            position: absolute;
            left: 0;
            right: 0;
            z-index: 22;
            box-shadow: 0 0 10px black;
            background: white;
            display: none;
            align-items: center;
            justify-content: space-between;
            padding: 14px 44px;

            &-column {
                display: block;
            }

            a {
                text-decoration: none;
                position: relative;
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 39px;
                letter-spacing: -6.7332e-05px;
                color: #000000;


                &::before {
                    content: "";
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    background: #B4E2E1;
                    border-radius: 50%;
                    border: 1px solid #4A90E2;
                    left: -24px;
                }
            }
        }



        .activeHome {
            &.active {
                background: rgba(255, 255, 255, 0.784618);
                position: fixed;
                width: 100%;
                height: 100vh;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                z-index: 20;

                & ~ .sliderBtn {
                    .btnSlider {
                        z-index: 21;
                    }
                    .btnBlock {
                        overflow: hidden;
                        display: flex;
                        
                    }
                }


                

                
            }
        }
    }

    &-content {
        &-header {
            border-bottom: 1px solid #D5D5D5;
            margin-bottom: 10px;
            padding: 10px 0;

            .new, .best  {
                font-style: normal;
                font-weight: 900;
                font-size: 24px;
                line-height: 30px;
                color: #9B9B9B;
                text-decoration: none;
                margin-right: 77px;
                position: relative;
                padding: 10px 0;
                transition: all ease 0.5s;
                
                &::before {
                    content: "";
                    position: absolute;
                    width: 0;
                    height: 2px;
                    margin: 0 auto;
                    background: #4E72CA;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    transition: all ease 0.5s;
                }
                
                &:hover {
                    color: #4E72CA;
                    transition: all ease 0.5s;


                    &::before {
                        width: 50%;
                        transition: all ease 0.5s;
                    }
                }

                &.active {
                    color: #4E72CA;
                    transition: all ease 0.5s;


                    &::before {
                        width: 50%;
                        transition: all ease 0.5s;
                    }
                }

                
            }
        }

        &-slider {
            background: rgba(182, 202, 251, 0.159986);
            box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);
            padding: 50px 0 ;
            margin-bottom: 26px;
        }
        

        .footer1 {
            padding: 18px 0;
            text-align: center;
            border-top: 1px solid rgba(182, 202, 251, 0.606827);
            display: flex;
            align-items: center;
            justify-content: center;

            h5 {
                font-size: 15px;
                line-height: 18px;
                color: #4E72CA;
                margin-right: 10px;
               
            }

            svg {
                width: 10px;
                height: 10px;
                fill: #4E72CA;
                transform: rotate(90deg);
            }
        }
        
    }
}    

