.block {
    border-radius: 10px;
    width: 359px;
    height: 238px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    padding: 25px; 
    box-sizing: border-box;

    .info {
        display: flex;
        flex-direction: column; 
        width: 60%;
        h3 {
            font-size: 23px;
            line-height: 26px;
            font-weight: 700;
            color: #000000;
            margin-bottom: 77px;
        }

        h5 {
            font-size: 15px;
            font-weight: 700;
            line-height: 18px;
            color: #4A4A4A;

            svg {
                width: 10px;
                height: 10px;
                fill: #4E72CA;
            }
        }
    }

    .img {
        width: 200px;
        height: 238px;
        position: absolute;
        right: -10px;
        display: flex;
        align-items: center;
        justify-content: center;

        
    }
}