.blockSms {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 440px;
    background: rgba(182, 202, 251, 0.125113);
    padding: 24px 14px;
    box-sizing: border-box;
    
    

    &.active{
        background: #fff;
    }
    
    &:hover {
        background: #fff;
    }
    &-left {
        display: flex;
        align-items: flex-start;

        &-img{
            display:flex;
            align-items: center;
            justify-content: center;
            margin-right: 16px;
        }

        h3 {
            font-style: normal;
            font-weight: 500;
            font-size: 15px;
            line-height: 18px;
            color: #25252B;
            margin-top: 5px;

        }
    
        span {
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 15px;
            letter-spacing: 0.2px;
            color: #25252B;
        }
    }

    &-right {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
        svg {

        }

        span {
            margin-top: 5px;
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 15px;
            text-align: right;
            letter-spacing: 0.2px;
            color: #686871;

        }
    }
}
