.productPrice {
    width: 100%;
      &-top {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          span {
              font-style: normal;
              font-weight: 500;
              font-size: 14px;
              line-height: 17px;
              letter-spacing: -5.89155e-05px;
              color: #414D6A;
          }
      }

      &-center {
          h2 {
              font-style: normal;
              font-weight: 500;
              font-size: 40px;
              line-height: 49px;
              letter-spacing: -0.00016833px;
              color: #444444;

          }

          &-links {
              a {
                  font-size: 14px;
                  font-weight: 800;
                  line-height: 16px;
                  letter-spacing: -5.89154e-05px;
                  text-decoration-line: underline;
                  color: #4E72CA;
              }
          }

          &-numbers {
              margin-top: 7px;
              background: #65E99F;
              border-radius: 18.5px;
              display: flex;
              align-items: center;
              width: 331px;
              padding: 9px 23px;
              position: relative;
              span{
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 20px;
                letter-spacing: 1px;
                color: #000000;
              }
              input{
                  font-family: "Gilroy";
                  background: transparent;
                width: 50px;
                font-style: normal;
                font-weight: 800;
                font-size: 15.5px;
                line-height: 20px;
                letter-spacing: 1px;
                color: #000000;
                border: none;
                outline: none;
                margin: 0 5px;
              }

              button {
                  cursor: pointer;
                  font-style: normal;
                  font-weight: normal;
                  font-size: 15px;
                  line-height: 18px;
                  color: #000000;
                  background: #41D481;
                  border-radius: 18.5px;
                  border: none;
                  height: 100%;
                  padding: 9px 23px;
                  box-sizing: border-box;
                  position: absolute;
                  right: 0;
              }
          }

          .author {
              margin-top: 17px;
              display: flex;
              align-items: center;
              h5 {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 17px;
                letter-spacing: -3.17237e-05px;
                color: #000000;
                margin-right: 7px;
              }

              a {
                font-style: normal;
                font-weight: bold;
                font-size: 14px;
                line-height: 17px;
                letter-spacing: -4.13787e-05px;
                text-decoration-line: underline;
                color: #2AAE63;
              }
          }
      }


      &-bottom {
          margin-top: 24px;

        button{
            cursor: pointer;
            font-style: normal;
            font-weight: 800;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: -5.89155e-05px;
            color: #FFFFFF;
            background: #304FFF;
            border-radius: 23.5px;
            padding: 15px 28px;
            border: none;
            margin-right: 11px;

            &:last-child {
                background: #4E72CA;
                border-radius: 23.5px;
            }
        }
      }
}